import http from '../../../../configs/httpConfig'

export const setPage = () => {
  return { type:'SETPAGE' }
}

export const clearData = () => {
  return {type:"CLEAR_DATA"} 
}

export const getAllUsersInit = () => ({
  type: 'GET_ALL_USERS_INIT'
})

export const getAllUsersFail = () => ({
  type: 'GET_ALL_USERS_FAIL'
})

export const fetchpage = (page) => {
  console.log(page)
  return (dispatch) => {
    dispatch({
      type:'FETCH_PAGE',
      data: page
    })
  }
}

export const getAllUsers = (page, dataPerPage = '') => {
  return async (dispatch) => {
    dispatch(getAllUsersInit())
    await http
      .get(`/user_details/user_list?page=${page}&limit=${dataPerPage}`)
      .then((response) => {
        dispatch({
          type: 'GET_ALL_USERS',
          data: response.data.data
        })
      })
      .catch(() => {
        dispatch(getAllUsersFail())
      })
  }
}

