 export const searchUsers = (full_name = '', mobile = '', email = '', page, dataPerPage = '') => {
  console.log(page)
  return (dispatch) => {
    http.get(`/user_details/user_list?full_name=${full_name}&mobile=${mobile}&email=${email}&page=${page}&limit=${dataPerPage}`)
    .then((response) => dispatch({ type:'SEARCH_USERS', data:response.data.data }))
    .catch((err) => { console.log(err) })
  }
}